import React, { FunctionComponent, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

import { Project } from '../../interface/project'
import { deleteProjectAsync } from '../../reducer/project/thunkaction'
import { RootState } from '../../store'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import FolderIcon from '@material-ui/icons/Folder';
import DeleteIcon from '@material-ui/icons/Delete';
import { Button, Header, Icon, Modal } from 'semantic-ui-react'

export const FileList: FunctionComponent = () => {

    const dispatch = useDispatch()

    const [isConfrimModal, setisConfrimModal] = useState(false)

    const [currentSelect, setcurrentSelect] = useState("")

    const projects = useSelector((state: RootState) => state.ProjectReducer.projects)

    const selectRemove = (id: string | undefined) => {
        if (id === undefined) id = ""
        setisConfrimModal(true)
        setcurrentSelect(id)
    }

    const confrimRemove = () => {
        alert(currentSelect)
        setisConfrimModal(false)
        if(currentSelect === "") return
        dispatch(deleteProjectAsync(currentSelect))
    }


    return (

        <List>
            {projects.map((p: Project) => (
                 <ListItem key={p.Id}>
                     <Link to={`/editor/${p.Id}`}>
                     <ListItemAvatar>
                   <Avatar>
                     <FolderIcon />
                   </Avatar>
                 </ListItemAvatar>
                        </Link>
                
                 <ListItemText
                   primary={p.ProjectName}
                   secondary={'Update at 10min ago'}
                 />
                 <ListItemSecondaryAction>
                   <IconButton edge="end" aria-label="delete" onClick={() => selectRemove(p.Id)}>
                     <DeleteIcon />
                   </IconButton>
                 </ListItemSecondaryAction>
               </ListItem>
             
            ))}
            <Modal
                basic
                onClose={() => setisConfrimModal(false)}
                onOpen={() => setisConfrimModal(true)}
                open={isConfrimModal}
                size='small'

            >
                <Header icon>
                    <Icon name='archive' />
        Archive Old Messages
      </Header>
                <Modal.Content>
                    <p>
                        Your inbox is getting full, would you like us to enable automatic
                        archiving of old messages?
        </p>
                </Modal.Content>
                <Modal.Actions>
                    <Button basic color='red' inverted onClick={() => setisConfrimModal(false)}>
                        <Icon name='remove' /> No
        </Button>
                    <Button color='green' inverted onClick={confrimRemove}>
                        <Icon name='checkmark' /> Yes
        </Button>
                </Modal.Actions>
            </Modal>
        </List>

    )

}


export default FileList